.calendar {
	
}

.calendar .row {
	box-shadow: 0 -1px 0 0 rgba(34,36,38,.15);
	position: relative;
}

.ui.card.event {
	position: absolute;
	z-index: 1;
}

.label {
	opacity: 0;
	transition: 0.5s;
}

.row:hover .label {
	opacity: 1;
	transition: 0.2s;
}
.time {
	float: right;
	padding-right: 5px;
}